import { BaseValueObject } from '../Base';

export const CHANGE_REASON_CODE_LIST = [
  'BUDGET_ERROR',
  'WORKING_MISTAKE',
  'CONSTRUCTION_CHANGE',
  'CONSTRUCTOR_MISTAKE',
  'PROCEDURE_MISTAKE',
  'TIME_LIMIT',
] as const;
export type CHANGE_REASON_CODE_TYPE = (typeof CHANGE_REASON_CODE_LIST)[number];

export class ChangeReasonCode extends BaseValueObject<
  'ChangeReasonCode',
  string
> {
  static from(value: string): ChangeReasonCode {
    return new ChangeReasonCode(value);
  }

  protected validate(value: string): string {
    if (!CHANGE_REASON_CODE_LIST.includes(value as CHANGE_REASON_CODE_TYPE)) {
      throw new Error('Invalid change reason code');
    }
    return value;
  }
}
